.about_banner {
    background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
    padding: 50px 0px;
    font-size: 50px;
    text-align: center;
    color: wheat;
    text-shadow: 2px 3px 3px gray;
}
.form-group {
    margin: 20px 0px;
}
input.form-control {
    height: 50px;
    display: block;
    width: 100%;
    background: #e6e6e6;
    font-family: Montserrat-Bold;
    font-size: 15px;
    line-height: 1.5;
    color: #666666;
    border-radius: 25px;
    padding: 0 30px 0 50px;
    outline: none;
    border: none;
}
textarea:focus, input:focus {
    border-color: transparent !important;
}
input.form-control , .submit-button {
    font-size: 18px !important;
    font-family: sans-serif !important;
    border-left-style: inset;
}   
textarea{
    border-radius: 25px !important;
    padding: 14px 30px;
    background: #e6e6e6 !important;
}
.submit-button {
    width: 100%;
    height: 50px;
    border-radius: 25px;
    background: #846add;
    font-family: Montserrat-Bold;
    font-size: 15px;
    line-height: 1.5;
    color: #fff;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 25px;
    -webkit-transition: all 0.4s;
    -o-transition: all 0.4s;
    -moz-transition: all 0.4s;
    transition: all 0.4s;
}
.contact100-form-title {
    display: block;
    text-align: center;
    font-family: Montserrat-ExtraBold;
    font-size: 30px;
    color: #333333;
    line-height: 1.2;
    padding-bottom: 15px;
    font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
    font-weight: bold;
    font-style: oblique;
}

.cus_form_table_col{
        /* border: 1px solid gray; */
        box-shadow: -1px 0px 5px #80808091;
        border-radius: 10px;
        padding: 20px;
}