h2:after {
    content: "";
    position: absolute;
    display: block;
    width: 50px;
    height: 3px;
    background: #149ddd;
    /* top: 0; */
    /* left: 0; */
}

.heading_tag {
    padding: 8px 1px;
    background-color: #fafafa;
}
.skill_section .heading_tag{
    text-align: center;
}
.skill_section span {
    font-weight: 600;
    margin-right: 8px;
    margin-left: 14px;
}
.mb_3{
    margin-bottom: 30px;
}
.position span {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 600;
}
.row.ux_ui_section ul li {
    margin: 17px 0px;
}
i.fa-solid.fa-angle-right {
    color: #4F94CD;
}
.info-card {
    background: #fff;
    /* text-align: center; */
    padding: 20px 30px;
    margin-bottom: 30px;
    border-radius: 3px;
    box-shadow: 0 10px 30px rgb(0 0 0 / 27%);

}
table tbody tr{
    vertical-align: top;
    margin-bottom: 20px;
}
.section-title h2 {
    /* text-align: center; */
    margin-top: 30px;
}
.about_banner {
    background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
    padding: 50px 0px;
    font-size: 50px;
    text-align: center;
    color: wheat;
    text-shadow: 2px 3px 3px gray;
}
/* reseure section border */
.resume .resume-item::before {
    content: "";
    position: absolute;
    width: 16px;
    height: 16px;
    border-radius: 50px;
    left: -9px;
    top: 0;
    background: #fff;
    border: 2px solid #1f5297;
}
.resume .resume-item {
    padding: 0 0 20px 20px;
    margin-top: -2px;
    border-left: 2px solid #1f5297;
    position: relative;
}
section#resume {
    padding: 100px 0px;
}
svg.social-svg {
    background: black;
}
/* media  768px*/

@media screen and (max-width:769px){
    img.img-fluid {
        width: 62%;
    }
    section#resume {
        padding: 28px 0px;
    }
}
