.Login-component {
    /* The image used */
    background-image: url(./IMG_2073.JPG);

    /* Full height */
    height: 100%; 

    /* Center and scale the image nicely */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
section#hero {
    position: absolute;
    top: 45%;
    /* left: auto; */
    width: 98%;
    text-align: center;
}
img.banner-img {
    width: 100%;
    position: relative;
    height: 600px;
    display: block;
    filter :brightness(0.5);
    object-fit: cover;
}
.hero-container.aos-init.aos-animate {
    padding: 10px 100px;
    background: #ffffff57;
    border-radius: 30px 2px 30px 2px;
}
i.fa-brands {
    color: #242323;
    width: 25px;
    font-size: 22px;
}